export default {
	props: {
		item: Object,
		index: {
			type: Number,
			default: 0
		},
		index1: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			
		};
	},
	methods: {
		// 关注
		follow (follow) {
			this.$emit('follow',{
				follow,
				index: this.index,
				index1: this.index1
			})
			uni.showToast({
				title: '关注成功'
			})
		},
		// 赞 踩
		operate (type) {
			this.$emit('operate',{
				type,
				index: this.index,
				index1: this.index1
			})
		},
		// 进入详情页
		openDetail () {
			uni.navigateTo({
				url:'../../pages/detail/detail?detailData='+JSON.stringify(this.item)
			})
		}
	}
}
